import { Component, OnInit } from '@angular/core';
import { PartyManageBean } from './bean/party-bean';
import { CommonHttpService } from '../../http/common.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
// import { OrgStatus, OrgTypes, OperatingLevel, LegalFlag } from '../../constant/common.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { UisftechCommonHttpService } from 'app/@uisftech/common/http/uisftech-common-http.service';

@Component({
    selector: 'party-manage',
    templateUrl: 'party-manage.html',
    styleUrls: ['party-manage.scss'],
    providers: [ConfirmationService, Commfunc, UisftechCommonHttpService]
})

/**
 * 机构管理
 */
export class PartyManage implements OnInit {
    creat_user: any;
    // 表格中显示的信息
    tabMesg: any = tableMessage;
    precredit: string = '机构管理';
    code: any;//码值
    //机构类型
    orgType: any[] = [];
    //机构状态
    orgStatus: any[] = [];
    //机构级别
    operatingLevel: any[] = [];
    //法人标识
    legalFlag: any[] = []
    // 表体内容
    tableData: any;
    partyManageBean: PartyManageBean = new PartyManageBean();
    //机构类型
    partyType: any = [];
    //机构状态 
    partyStatus: any = [];
    // 机构等级
    partyLevel: any = [];


    //总页数
    total: string;
    //每页多少条
    pageSize: string = '10';

    //提示信息
    msgs: Message[] = [];

    //模态框的标题
    headerTitle: string = "机构同步";
    //模态框是否显示
    display: boolean = false;
    //模态框
    showModel: string;
    //传出的值
    inValue: string;
    //接收后台的数据
    list: any[]
    list1: any[]
    currentPage: any;
    //
    setOrgLValue: any
    first: number = 0;
    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public uisftechCommonHttpService: UisftechCommonHttpService,

    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.orgType = this.code['OrgTypes'];
        this.orgStatus = this.code['OrgStatus'];
        this.operatingLevel = this.code['OperatingLevel'];
        this.legalFlag = this.code['LegalFlag']
    }

    ngOnInit() {
        this.partyManageBean.rowNum = '10';
        this.partyManageBean.startRow = '1';
        // this.partyManageBean.operatingLevel = 'TEAM'
        this.queryClick();
        // this.commonHttpService.queryVagueOrgList(this.partyManageBean).subscribe(data => {
        //     if (data.orgList) {
        //         this.list = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
        //         this.list1 = this.commfunc.listToLabelvalue(data.orgList, 'orgId', 'orgName');
        //     }
        // })

        let _thiss = this;
        document.onkeydown = function (event) {
            if (event && event.keyCode === 13) {
                _thiss.query();
            }
        }
    }
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //onchange事件
    doChose(event) {
        this.partyManageBean.orgName = this.returnValue(event.value, 'value', 'label');
    }
    doChose1(event) {
        this.partyManageBean.orgId = this.returnValue(event.value, 'label', 'value');
    }

    returnValue(param, colum, valueColum) {
        let value: string = "";
        this.list.forEach(element => {
            if (element[colum] === param) {
                value = element[valueColum];
            }
        });
        return value;
    }
    //查询
    queryClick() {
        this.creat_user = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        // let data = {
        //     tellerId: this.creat_user,
        // };
        // this.uisftechCommonHttpService.searchMaxPostOrgIdByUserId(data).subscribe(data => {
        //     if (data.returnCode.code == "success") {
        //         this.partyManageBean.orgId = data.orgId;
        //         let datas = {
        //             orgId: this.partyManageBean.orgId,
        //             relationshipType: '0',
        //         };
        //         this.uisftechCommonHttpService.queryOrgByOrgIdAndRelationship(datas).subscribe(data => {
        //             if (data.returnCode.code == "success") {
        //                 this.partyManageBean.orgName = data.orgList[0].orgName;
        this.partyManageBean.flag = '0';
        this.commonHttpService.queryOrgByCombConditionsLevel(this.partyManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.orgInfoList;
                this.total = data.rowCount;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message  });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
        //             }
        //         })
        //     } else {
        //         this.msgs = [];
        //         this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        //     }
        // })
    }
    query() {//点击查询
        this.partyManageBean.flag = '0';
        this.first = 0;
        this.partyManageBean.startRow = "1";
        this.commonHttpService.queryOrgByCombConditionsLevel(this.partyManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.orgInfoList;
                this.total = data.rowCount;
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    /**
     * 
     * @param event 机构同步
     */

    // 机构同步
    followMe() {
        this.headerTitle = '机构同步';
        this.display = true;
        this.showModel = '1';
    }

    //添加后返回的值
    addCall(param) {
        this.display = param;
        // this.partyManageBean.startRow = '1';
        this.queryClick();
    }
    //机构级别配置
    setOrgLevel(item) {
        console.log(item);
        this.headerTitle = '机构级别配置';
        this.display = true;
        this.showModel = '2';
        this.setOrgLValue = item;
    }


    //修改的回调
    setOrgLCall(param) {
        this.display = param;
        // this.partyManageBean.startRow = '1';
        // this.partyManageBean = new PartyManageBean();
        this.queryClick();
    }
    //分页
    paginate(event) {

        //每页显示的条数
        this.partyManageBean.rowNum = event.rows;
        //当前页
        this.currentPage = event.page + 1 + '';
        this.partyManageBean.startRow = this.currentPage;
        this.first = event.page * event.rows;
        // this.first=event.page;
        //调用查询的方法
        this.queryClick();
    }

    // 重置
    reset() {
        this.partyManageBean.orgName = '',
        this.partyManageBean.orgId = '',
        this.partyManageBean.superiorManageOrg = '';
        this.partyManageBean.orgType = '';
        this.partyManageBean.orgStatus = '';
        this.partyManageBean.operatingLevel = '';

    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}