codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code是自己声明的变量
  }
  // 机构树回调
  treeCall(param) {
    this.orgTreeNameList = [];
    this.partyManageBean.mechanismList = [];
    param.forEach(item => {
      this.partyManageBean.mechanismList.push(item.orgId);
      this.orgTreeNameList.push(item.orgName);
    })
    let params = {
      orgIdList: this.partyManageBean.mechanismList,
      operatingLevel: this.partyManageBean.operatingLevel
    }
    this.commonHttpService.batchCreateOrgLevel(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }
