import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from '../../../../../../../app/sharedBasis.module';
import { PartyManageBean } from "../bean/party-bean";
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../http/common.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

// import { LegalFlag } from '../../../constant/common.codeValue' 
@Component({
  selector: 'app-party-in-step',
  templateUrl: './party-in-step.component.html',
  styleUrls: ['part-in-step.component.scss'],
  providers: [CommonHttpService, Commfunc]
})
export class PartyInStepComponent implements OnInit {
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();

  public userform: FormGroup; //校验

  //提示信息
  public msgs: Message[] = [];
  //银行标识
  legalFlag: any[] = [];
  code: any;
  public partyManageBean: PartyManageBean = new PartyManageBean();
  constructor(
    public commonHttpService: CommonHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) {

    this.codeValues() //调用方法，获取全部码值
    this.legalFlag = this.code['LegalFlag'];
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'orgId': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
      'legalFlag': new FormControl('', Validators.required),
    });
  }
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  // 确定
  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    if (this.partyManageBean.orgId == '0000' || this.partyManageBean.orgId == 'T0000' || this.partyManageBean.orgId == 'C0000') {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '0000，T0000，C0000为虚拟机构，无法同步！' });
      return;
    }
    //提交服务
    this.commonHttpService.syncPartyGroup(this.partyManageBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  //取消
  cancel() {
    this.outValue.emit(false);
    this.partyManageBean = new PartyManageBean();
  }

}